import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import { SynopsisContainer } from './style';

const propTypes = {};

const defaultProps = {};

export default function SynopsisSection({ synopsis, poster }) {
  return (
    <SynopsisContainer>
      <Row>
        <Col xs={{ size: 12, order: 2 }} sm={{ size: 12, order: 2 }} md={{ size: 9, order: 1 }} className="synopsis">
          {synopsis}
        </Col>
        <Col xs={{ size: 12, order: 1 }} sm={{ size: 12, order: 1 }} md={{ size: 3, order: 2 }} className="poster">
          <img src={poster} alt="" style={{ width: '100%' }} />
        </Col>
      </Row>
    </SynopsisContainer>
  );
}

SynopsisSection.propTypes = propTypes;
SynopsisSection.defaultProps = defaultProps;